@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,700&display=swap);
:root{--btnBg: royalblue;--btnColor: #fff;--paddingBlock: 20px }.app{font-family:"Roboto",sans-serif;font-weight:400;font-size:32px;text-align:center}.letter{color:#4169e1;font-weight:700;margin:0 0 1em}.btn{margin:0 5px;font-size:16px;padding:1em 2em;background:royalblue;background:var(--btnBg);border:2px solid royalblue;border:2px solid var(--btnBg);color:#fff;color:var(--btnColor);border-radius:5px;cursor:pointer;transition:.5s ease}.btn:hover{background-color:transparent;color:#000}.btn:disabled{opacity:.5;-webkit-user-select:none;-ms-user-select:none;user-select:none;background-color:royalblue;background-color:var(--btnBg);color:#fff;color:var(--btnColor);cursor:no-drop}h2{margin:0 0 1em;font-size:30px}.settings{margin:20px 0;text-align:left;font-size:16px;padding:20px;padding:var(--paddingBlock);border:1px solid #4169e1;border-radius:10px}.settings label{display:block;margin:20px 0}.past-letters{text-align:left;margin:20px 0;padding:20px;padding:var(--paddingBlock);border:1px solid #4169e1;border-radius:10px}
